.home-navbar {
  /* width: 100vw; */
  height: 9vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 2vw 0px 5.5vw;
}
.home-navbar-name {
  font-family: Poppins;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 48px;
  color: #666666;
}
.home-navbar-freelance {
  font-family: Space Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  margin-left: 11.5vw;
  color: #191a1e;
}
.home-navbar-nav {
  font-family: Space Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 36px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 21vw;
  color: #666;
  text-decoration: none;
}
.home-page1 {
  /* width: 100vw; */
  display: flex;
  align-items: center;
  height: 88vh;
  justify-content: space-between;
}
.home-page1-navigation {
  padding: 30vh 65px 120px;
